import React from 'react';
import s from './Post.module.css';

const Post = (props) => {
  return (
    <div className={s.item}>
      <img src='data:image/png;base64,' />
      {props.message}
      <div className={s.likes__block}>
        <span>like</span>
        {props.likesCount}
      </div>
    </div>
  );
}

export default Post;